/*--------------------------------------------------------------
1.0 Cross browser CSS3 mixins
--------------------------------------------------------------*/
/*--------------------------------------------------------------
2.0 BOX-SIZING
--------------------------------------------------------------*/
/* This mixin sets box-sizing and handle the prefixes once: div { @include box-sizing(border-box); } */
/*--------------------------------------------------------------
3.0 Opacity
--------------------------------------------------------------*/
/* Frequently, you want to set not only vendor prefixes but use different syntax altogether. This mixin sets opacity for current browsers and older IE: div { @include opacity(0.5); } */
/*--------------------------------------------------------------
4.0 Column-width
--------------------------------------------------------------*/
/* column-width is one of the best examples of a mixin taking care of browser prefixes: div { @include column-width(150px); } */
/*--------------------------------------------------------------
5.0 Circle
--------------------------------------------------------------*/
/* Once you’ve set up a mixin like border-radius, you can use it in other mixins, in this case to make a rectangular div circular: div { @include circle(); } */
/*--------------------------------------------------------------
6.0 FontFace
--------------------------------------------------------------*/
/* source: https://github.com/magnetikonline/sass-boilerplate/blob/master/fontface.scss */
/*--------------------------------------------------------------
7.0 Font Size
--------------------------------------------------------------*/
/* This mixin sets the font-size in pixels, then overwrites that by setting it in rems. rems will only be used if the browser in question supports them: div { @include font-size(14px); } */
/*--------------------------------------------------------------
8.0 Box-shadow
--------------------------------------------------------------*/
/* This great mixin lets you specify the values of a box-shadow once, without worrying about vendor prefixes: div { @include box-shadow(8px, 8px); } */
/*--------------------------------------------------------------
9.0 xPos
--------------------------------------------------------------*/
/* This mixin provides a shorthand for positioning an element along the x axis: div { @include xPos(50px); @include yPos(50px); } */
/*--------------------------------------------------------------
10.0 Vertical Align
--------------------------------------------------------------*/
/* Vertically aligning elements is challenging even with CSS, but this mixin gives you a nice simple solution: div { @include vertical-align(); } */
/*--------------------------------------------------------------
11.0 Flexbox
--------------------------------------------------------------*/
/* Sass mixins are great for handling settings like flexbox, that have had several different syntaxes published at different times. div { @include flexbox(); } */
/*--------------------------------------------------------------
12.0 Flex-direction
--------------------------------------------------------------*/
/* Mixins also allow us to use Sass’ @if, @else if, and @else statements to combine what could have been two separate mixins into one: div { @include flex-direction(column); } */
/*--------------------------------------------------------------
13.0 Flex-grow
--------------------------------------------------------------*/
/*--------------------------------------------------------------
14.0 justify-content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
15.0 align-content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
16.0 align-items
--------------------------------------------------------------*/
/*--------------------------------------------------------------
17.0 align-self
--------------------------------------------------------------*/
/*--------------------------------------------------------------
18.0 Gradient
--------------------------------------------------------------*/
/* We should always try to keep our code simple, but when necessary mixins can be huge. This mixin allows us to create different kinds of gradient for all browsers, using just three values: div { @include gradient(#ff00ff, #ff00cc, vertical); }*/
