/*--------------------------------------------------------------
3.0 Typography
--------------------------------------------------------------*/
/*--------------------------------------------------------------
4.0 Base Style
--------------------------------------------------------------*/
body { font-family: "Roboto", sans-serif; font-weight: 400; color: #222; background-color: #fff; font-size: 100%; font-size: 1em; }

h1, h2, h3, h4, h5, h6 { display: inline-block; font-family: "Roboto", sans-serif; font-weight: 300; color: #222; }

h1 { font-size: 32px; font-size: 2em; }

a { color: #dfb948; font-weight: 300; -webkit-transition: all 0.4s ease-in-out; -moz-transition: all 0.4s ease-in-out; -ms-transition: all 0.4s ease-in-out; -o-transition: all 0.4s ease-in-out; }

a:hover { transition: 0.3s; cursor: pointer; }

a:focus { text-decoration: none; }

a.dotted-link { border-bottom: 1px dotted #778492; color: #dfb948; }

a.dotted-link:hover { text-decoration: none; transition: 0.3s; }

p { font-family: "Roboto", sans-serif; font-weight: 300; color: #222; line-height: 1.61; }

strong { font-weight: bold; }

/* ---------------------------------------------------------------------------- Components (also called Partials) ------------------------------------------------------------------------- */
/*--------------------------------------------------------------
5.0 Animations
--------------------------------------------------------------*/
/*** 1. General Animations classes 2. Login-form 3. Token-form 4. cover-form */
/*---------------------------*/
/* General Animation classes */
/*---------------------------*/
.fade-in { -webkit-animation: opacity 0.5s; -moz-animation: opacity 0.5s; -ms-animation: opacity 0.5s; -o-animation: opacity 0.5s; animation: opacity 0.5s; }

@-webkit-keyframes opacity { from { opacity: 0; }
  to { opacity: 1; } }

@-moz-keyframes opacity { from { opacity: 0; }
  to { opacity: 1; } }

@-ms-keyframes opacity { from { opacity: 0; }
  to { opacity: 1; } }

@-o-keyframes opacity { from { opacity: 0; }
  to { opacity: 1; } }

@keyframes opacity { from { opacity: 0; }
  to { opacity: 1; } }

.fade-out { -webkit-animation: fadeOut 0s ease-in 5s forwards; -moz-animation: fadeOut 0s ease-in 5s forwards; -ms-animation: fadeOut 0s ease-in 5s forwards; -o-animation: fadeOut 0s ease-in 5s forwards; animation: fadeOut 0s ease-in 5s forwards; }

@-webkit-keyframes fadeOut { to { width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden; } }

@-moz-keyframes fadeOut { to { width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden; } }

@-ms-keyframes fadeOut { to { width: 0; height: 0; margin: 0; padding: 0; overflow: hidden; } }

@-o-keyframes fadeOut { to { width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden; } }

@keyframes fadeOut { to { width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    overflow: hidden; } }

.animateBottomLogin { -webkit-animation: animateBottomLogin 0.8s ease-out; -moz-animation: animateBottomLogin 0.8s ease-out; -ms-animation: animateBottomLogin 0.8s ease-out; -o-animation: animateBottomLogin 0.8s ease-out; animation: animateBottomLogin 0.8s ease-out; }

@keyframes animateBottomLogin { from { top: 130%;
    opacity: 0.5; }
  to { top: 50%;
    opacity: 1; } }

/*--------------------------------------------------------------
6.0 Buttons
--------------------------------------------------------------*/
.button { font-family: "Roboto", sans-serif; text-transform: uppercase; outline: 0; background-color: #dfb948; border: 1px solid #dfb948; width: 100%; padding: 15px; color: #fff; font-size: 14px; -webkit-transition: all 0.4s ease; transition: all 0.4s ease; cursor: pointer; }

.button:active, .button:focus, .button:hover, .button:visited { background-color: #fff; color: #dfb948; }

.button-close { position: absolute; top: 0.5em; right: 1em; color: #222; }

.button-close:active, .button-close:focus, .button-close:hover, .button-close:visited { color: #728187; cursor: pointer; }

.form-about .button { display: inline-block; width: auto; padding: 1em; }

@media (min-width: 48em) { .form-about .button { margin-top: 2em; float: right; clear: both; } }

.button-image { display: block; position: absolute; bottom: 8px; width: 100%; padding: 1em; text-shadow: none; background-color: rgba(114, 129, 135, 0.5); color: #fff; }

.button-image:active, .button-image:focus, .button-image:hover, .button-image:visited { background-color: rgba(114, 129, 135, 0.8); }

.button-cover { position: absolute; top: 0; right: 0; padding: 1em; text-shadow: none; background-color: rgba(114, 129, 135, 0.5); color: #fff; }

.button-cover:active, .button-cover:focus, .button-cover:hover, .button-cover:visited { background-color: rgba(114, 129, 135, 0.9); }

.reset-password { display: block; }

/* The close button */
.closebtn { margin-left: 15px; color: white; font-weight: bold; float: right; font-size: 22px; line-height: 20px; cursor: pointer; transition: 0.3s; }

/* When moving the mouse over the close button */
.closebtn:hover { color: black; }

.js-hidden { display: none; transition: all 1s linear; transition-property: display; transition-duration: 2s; }

.js-show { display: block; transition: all 1s linear; transition-property: display; transition-duration: 2s; }

/*--------------------------------------------------------------
7.0 Modal
--------------------------------------------------------------*/
.modal { z-index: 1; background: #FFFFFF; padding: 45px; box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24); /* Vertically center */ position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) !important; opacity: 1; }

.modal-container { z-index: 4; display: none; padding-top: 100px; position: fixed; left: 0; top: 0; width: 100%; height: 100%; overflow: auto; background-color: rgba(0, 0, 0, 0.5); box-shadow: 10px 10px 16px 0 rgba(0, 0, 0, 0.1); }

#create-modal, #login-modal, #token-modal { display: block; }

#notice { padding: 20px; background-color: green; color: #fff; margin-bottom: 15px; }

/*--------------------------------------------------------------
8.0 Icons
--------------------------------------------------------------*/
.icon { width: 4%; height: auto; padding-right: 0.25em; }

.edit-icons { padding-right: 8px; }

.edit-icons svg { fill: #fff; }

.icon-login svg { fill: #f62405 !important; width: 4% !important; }

.icon-footer svg { width: 24px; height: auto; padding-right: 0.25em; padding-left: 0.25em; }

.social-links { margin-top: 0.5em; }

@media (min-width: 64em) { .social-links { margin-top: 1em; } }

.social-links a { padding: 8px; }

.social-links svg { fill: #fff; width: 24px; }

.social-links svg:hover { -webkit-transition: all, 0.4s, ease-in-out; -moz-transition: all, 0.4s, ease-in-out; -ms-transition: all, 0.4s, ease-in-out; -o-transition: all, 0.4s, ease-in-out; transition: all, 0.4s, ease-in-out; -webkit-transform: translateY(-1px); -moz-transform: translateY(-1px); -ms-transform: translateY(-1px); transform: translateY(-1px); }

/*--------------------------------------------------------------
9.0 Images
--------------------------------------------------------------*/
.container-image { position: relative; }

@media (min-width: 48em) { .container-image { margin-bottom: -4.6875em; max-width: 17.5em; } }

.container-image img { border: 3px solid #fff; box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.1); }

/*--------------------------------------------------------------
10.0 Cover
--------------------------------------------------------------*/
/* Navigation */
.sidenav { height: 100%; width: 0; position: fixed; z-index: 3; top: 0; left: 0; background-color: #728187; opacity: 1; overflow-x: hidden; transition: 0.5s; padding-top: 60px; }

.sidenav a { padding: 8px 8px 8px 32px; text-decoration: none; font-size: 25px; color: #fff; display: block; transition: 0.3s; }

.sidenav a:hover { color: #f1f1f1; }

.sidenav .closebtn { position: absolute; top: 0; left: -1.875em; font-size: 1em; }

#main { transition: margin-left .5s; padding: 16px; }

@media screen and (max-height: 450px) { .sidenav { padding-top: 15px; }
  .sidenav a { font-size: 18px; } }

/* ---------------------------------------------------------------------------- Layout ------------------------------------------------------------------------- */
/*--------------------------------------------------------------
11.0 Header
--------------------------------------------------------------*/
header { padding: 16px; text-align: center; }

.sr-only { position: absolute; left: -10000px; width: 1px; height: 1px; top: auto; overflow: hidden; }

.vcard-header { padding: 3.125em; text-align: center; color: #fff; text-shadow: 2px 2px #222; }

.overlay { position: relative; }

.overlay::before { position: absolute; content: ""; top: 0; left: 0; width: 100%; height: 100%; background-color: #222; opacity: 0.4; }

.title-container h2 { margin-top: 1.5em; margin-bottom: 0.5em; font-size: 2em; color: #fff; }

@media (min-width: 64em) { .title-container h2 { margin-top: 2em; font-size: 2.5em; } }

#quotesDisplay { margin-bottom: 1em; font-size: 1em; color: #fff; }

@media (min-width: 48em) { #quotesDisplay { font-size: 1.5em; } }

#quotes-input { display: none; }

/*--------------------------------------------------------------
12.0 Footer
--------------------------------------------------------------*/
